import { Component } from 'react'

const MAX_HEAD_TILT_X = 400
const MAX_HEAD_TILT_Y = 400

export default class extends Component {
  state = {
    transform: `scaleY(1.2) translateZ(${-this.props.size}px) rotateX(-40deg)
  rotateY(-45deg)`,
  }

  logo = React.createRef()

  onMouseMove = event => {
    const { clientX, clientY } = event
    const { size } = this.props

    const vector = {
      x: saturate(clientX - this.center.x, MAX_HEAD_TILT_X),
      y: saturate(clientY - this.center.y, MAX_HEAD_TILT_Y),
    }

    // const polar = {
    //   r: Math.sqrt(vector.x * vector.x + vector.y * vector.y),
    //   a: Math.atan(vector.y / vector.x),
    // }

    this.setState({
      transform: `scaleY(1.2) translateZ(${-size}px) rotateX(calc(-40deg - 0.001 * ${
        vector.y
      }rad)) rotateY(calc(-45deg + 0.001 * ${vector.x}rad))`,
    })
  }

  componentDidMount() {
    const { x, width, y, height } = this.logo.current.getBoundingClientRect()
    this.center = {
      x: x + width / 2,
      y: y + height / 2,
    }

    document.addEventListener('mousemove', this.onMouseMove)
  }

  componentWillUnmount() {
    document.removeEventListener('mousemove', this.onMouseMove)
  }

  render() {
    const { size, letters = 'IC' } = this.props
    const { transform } = this.state
    const [left, right] = letters

    return (
      <div className="logo" ref={this.logo}>
        <div className="cube">
          <div className="cube__face cube__face--front">
            <img src={require(`./faces/${left}.svg`)} alt={left} />
            <img src={require('./eye.svg')} alt="eye" />
          </div>
          <div className="cube__face cube__face--right">
            <img src={require(`./faces/${right}.svg`)} alt={right} />
          </div>
        </div>

        <style jsx>{`
          .logo {
            width: ${size}px;
            perspective: ${size}px;
            margin-top: -1rem;
          }

          .cube {
            height: ${size}px;
            transform-style: preserve-3d;
            transform: ${transform};
            transition: ease-out 0.2s;
          }

          .cube__face {
            position: absolute;
            width: 100%;
            height: 100%;
            transform-origin: bottom center;
          }

          .cube__face > img {
            position: absolute;
          }

          .cube__face--front {
            transform: rotateY(0deg) translateZ(${size / 2}px) skew(12deg);
          }

          .cube__face--right {
            transform: rotateY(90deg) translateZ(${size / 2}px) skew(-12deg);
          }
        `}</style>
      </div>
    )
  }
}

function saturate(num, limit) {
  if (num < -limit) return -limit
  if (num > limit) return limit
  return num
}
